.comment {
  width: calc(100% - 4rem);
  align-items: center;
  margin-left: 4rem;
  display: flex;
}

.comment p {
  flex-grow: 1;
  gap: .25rem;
  display: block;
}

.comment .author {
  flex-direction: column;
  align-items: center;
  margin-right: 1rem;
  display: flex;
}

.pull-request {
  align-items: center;
  display: flex;
}

.comment .author img, .pull-request img {
  width: 3rem;
  height: 3rem;
  border-radius: 2rem;
  flex-shrink: 0;
}

.comment a {
  color: #fff;
  width: 10rem;
  text-align: center;
  background-color: green;
  border-radius: .25rem;
  flex-shrink: 0;
  margin: .25rem;
  padding: .25rem;
  text-decoration: none;
}

.comment a:hover {
  background-color: red;
}

